import React from 'react'
import { Hero, HeroBody, Container, Title, Table } from 'bloomer'
import moment from 'moment';

const nextTrain = (time, freq) => {
    let minutes = new Date(moment(time, "HH:mm")).getSeconds();
    return minutes;
}

const TrainTable = (props) => (
  <Hero isColor="warning">
    <HeroBody>
      <Table isBordered isStriped>
        <thead>
          <tr>
            <th>Train Name</th>
            <th>Destination</th>
            <th>Frequency (Min)</th>
            <th>Next Arrival</th>
            <th>Minutes Away</th>
          </tr>
        </thead>
        <tbody>
          {props.data.map((train, key) => {
            return(
          <tr key={key}>
            <th>{train.name}</th>
            <th>{train.destination}</th>
            <th>{train.freq}</th>
            <th>{nextTrain(train.time)}</th>
            <th>{'00'}</th>
          </tr>
            )
          })}
        </tbody>
      </Table>
    </HeroBody>
  </Hero>
)

export default TrainTable;



