import React, { Component } from 'react'
import {
  Hero,
  HeroBody,
  Container,
  Title,
  Field,
  Label,
  Control,
  Input,
  Columns,
  Column,
  Button
} from 'bloomer'
import base from '../base'
import TrainTable from '../components/TrainTable'

const INITIAL_STATE = {
  name: '',
  destination: '',
  time: '',
  freq: '',
  data: [],
}

const byPropKey = (propertyName, value) => () => ({
  [propertyName]: value,
})

class IndexPage extends React.Component {
  constructor() {
    super()
    this.state = { ...INITIAL_STATE }
  }

  componentDidMount() {
    base.bindToState('routes', {
      context: this,
      state: 'data',
      asArray: true,
    })
  }

  onSubmit = (event) => {
    event.preventDefault;
    base.push('routes', {
      data: {
        name: this.state.name,
        destination: this.state.destination,
        time: this.state.time,
        freq: this.state.freq,
      }
    });
  }

  render() {
    return (
      <div>
        <Hero isColor="light">
          <HeroBody>
            <Container hasTextAlign="centered">
              <Title>Train Times Choo Choo</Title>
            </Container>
          </HeroBody>
        </Hero>
        <TrainTable data={this.state.data} />
        <Hero isColor="black">
          <HeroBody>
              <form onSubmit={this.onSubmit}>
            <Columns isCentered>
                <Column isSize="1/5">
                  <Field>
                    <Label>Train Name</Label>
                    <Control>
                      <Input
                        type="text"
                        placeholder="Train Name"
                        onChange={event =>
                          this.setState(byPropKey('name', event.target.value))
                        }
                      />
                    </Control>
                  </Field>
                </Column>
                <Column isSize="1/5">
                  <Field>
                    <Label>Destination</Label>
                    <Control>
                      <Input
                        type="text"
                        placeholder="Destination"
                        onChange={event =>
                          this.setState(
                            byPropKey('destination', event.target.value)
                          )
                        }
                      />
                    </Control>
                  </Field>
                </Column>
                <Column isSize="1/5">
                  <Field>
                    <Label>First Train Time</Label>
                    <Control>
                      <Input
                        type="text"
                        placeholder="First Train Time"
                        onChange={event =>
                          this.setState(byPropKey('time', event.target.value))
                        }
                      />
                    </Control>
                  </Field>
                </Column>
                <Column isSize="1/5">
                  <Field>
                    <Label>Frequency</Label>
                    <Control>
                      <Input
                        type="text"
                        placeholder="Frequency"
                        onChange={event =>
                          this.setState(byPropKey('freq', event.target.value))
                        }
                      />
                    </Control>
                  </Field>
                </Column>
                <Column isSize="1/5">
                  <Field>
                    <Label>.</Label>
                    <Button type='submit'>Submit</Button>
                  </Field>
                </Column>
            </Columns>
              </form>
          </HeroBody>
        </Hero>
      </div>
    )
  }
}

export default IndexPage



